.sidebar{
  height:600px;
  width:15%;
  top:20px;
  display:block;
  text-align: center;
  border-radius: 10px;
  position:fixed;
  color: aliceblue;
  right:12px;
  padding-right:30px;
  padding-top:90px;
  background-color:rgb(39, 41, 41);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.sidelink{
  text-decoration: none;
  color:white;
}

.sidelist{
  list-style-type:none;
  padding-top:25px;
}
.sidebar .sidelink:hover{
  color:rgb(70, 139, 167);
}

.card{
  color: rgb(185, 203, 198);
  text-align: center;
  background-repeat:no-repeat;
  background-size: cover;
  width: 950px;
  height:250px;
  border-radius: 15px;
  box-shadow:10px  20px  40px rgb(135, 49, 22);
  padding-top:10px ;
  margin-top: 50px;
  margin-left:100px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.footer{
